import { useCallback, useEffect, useState } from "react";
let logoutTimer;

const useAuth = () => {
    const [token, setToken] = useState(null);
    const [userId, setUserId] = useState(null);
    const [expirationDateTime, setExpirationDateTime] = useState(null)

    const login = useCallback((userId, token) => {
        setUserId(userId);
        setToken(token)
        // new Date().getTime() => will give current date time in ms
        //  new Date().getTime() + 1000 * 60 * 60 => will add 1 hour in ms
        // const tokenExpirationDate : will contain the time in ms after one hour when the user logged in
        const tokenExpirationDate = new Date(new Date().getTime() + 1000  * 60 * 60)
        setExpirationDateTime(tokenExpirationDate)
        // toISOString => ensure that no data is lost when date is stringified
        localStorage.setItem('userData', JSON.stringify({ userId: userId, token: token, expiration: tokenExpirationDate.toISOString() }))

    }, [])


    const logout = useCallback(() => {
        setToken(null);
        setUserId(null);
        setExpirationDateTime(null);
        localStorage.removeItem('userData')
    }, [])


    useEffect(() => {
        if (token && expirationDateTime) {

            const remainingDateTime = expirationDateTime.getTime() - new Date().getTime();
            // logoutTimer : will contain the id of the timer
            logoutTimer = setTimeout(() => {
                logout();
            }, remainingDateTime);
        } else {
            clearTimeout(logoutTimer); // must clear timer because we might click manually on logout , in result the token will be cleared
            // thus we dont want to have timer to logout while we are logged out
        }
    }, [token, logout])


    useEffect(() => {
        // code line below means if i get an object it will be destructured via {}
        // if i dont get an object but a value of null or undefined it will destructure an empty object 
        // localStorageToken and userId both will be null 
        const { token: localStorageToken, userId, expiration } = JSON.parse(localStorage.getItem('userData')) ?? {};
        const expirationDateTime = new Date(expiration);
        const currentDateTime = new Date();

        // !token : means im not logged in
        // lsToken : means that that a token is stored in local storage and i can use it to auto login 
        // expirationDateTime > currentDateTime : if the expiration date time of the token is greater than the current date time
        // we want to auto login otherwise the token is expired and is no longer valid 
        if (!token && localStorageToken && expirationDateTime > currentDateTime) {
            setToken(localStorageToken);
            setUserId(userId);
        }


    }, []);

    return {token, login, logout, userId}
}

export default useAuth;