import React, { Suspense } from "react";
import { BrowserRouter as Router, Route, Routes, redirect, Navigate } from "react-router-dom";
// import Users from './Users/Pages/Users.jsx'
// import NewPlace from './Places/Pages/NewPlace.jsx'
// import UpdatePlace from './Places/Pages/UpdatePlace.jsx'
import MainNavigation from "./Shared/Components/Navigation/MainNavigation.jsx";
// import UserPlaces from "./Places/Pages/UserPlaces.jsx";
// import Auth from "./Users/Pages/Auth.jsx";
import AuthContext from "./Shared/context/auth-context.js";
import useAuth from "./Shared/hooks/auth-hook.js";
import Spinner from "./Shared/Components/UIElements/Spinner.jsx";

const Users = React.lazy(() => import('./Users/Pages/Users.jsx'))
const NewPlace = React.lazy(() => import('./Places/Pages/NewPlace.jsx'))
const UserPlaces = React.lazy(() => import('./Places/Pages/UserPlaces.jsx'))
const UpdatePlace = React.lazy(() => import('./Places/Pages/UpdatePlace.jsx'))
const Auth = React.lazy(() => import('./Users/Pages/Auth.jsx'))

function App() {
  const { login, logout, token, userId } = useAuth()

  let routes;
  if (token) {
    routes = (
      <>

        <Route path='/places/new' element={<NewPlace />} />
        <Route path='/places/:placeId' element={<UpdatePlace />} />
      </>
    )
  } else {
    routes = <Route path='/auth' element={<Auth />} />

  }
  return (
    <Router>
      <AuthContext.Provider value={{ isLoggedIn: !!token, login: login, logout: logout, userId: userId, token: token }}>
        <MainNavigation />
        <main>
          <Suspense fallback={<div className="center"><Spinner asOverlay /></div>}>

            <Routes>
              <Route path='/' element={<Users />} />
              {routes}
              <Route path='/:userId/places' element={<UserPlaces />} />
              <Route
                path="*"
                element={<Navigate to="/" />}
              />
            </Routes>
          </Suspense>
        </main>
      </AuthContext.Provider>
    </Router>
  );
}

export default App;
