import React from 'react';
import { useState } from 'react';
import { Link } from 'react-router-dom';

import MainHeader from './MainHeader';
import NavLinks from './NavLinks';
import SideDrawer from './SideDrawer';
import BackDrop from '../UIElements/BackDrop';
import './MainNavigation.css';

const MainNavigation = props => {
  const [showSideDrawer, setShowSideDrawer] = useState(false);
  return (
    <>
      {showSideDrawer && <BackDrop click={() => setShowSideDrawer(false)}>  </BackDrop>}

     
      <SideDrawer  showSideDrawer={showSideDrawer} click={() => setShowSideDrawer(false)}>
        <nav className='main-navigation__drawer-nav'>
          <NavLinks />
        </nav>
      </SideDrawer>
      <MainHeader>
        <button className="main-navigation__menu-btn" onClick={() => setShowSideDrawer(true)}>
          <span />
          <span />
          <span />
        </button>
        <h1 className="main-navigation__title">
          <Link to="/">YourPlaces</Link>
        </h1>
        <nav className='main-navigation__header-nav'>
          <NavLinks />
        </nav>
      </MainHeader>
    </>
  );
};

export default MainNavigation;
